import React, { Component } from "react";
import "../layouts/index.css";

import mixpanel from "../lib/mixpanel/index";
import Layout from "../components/layout";
import SEO from "../components/seo";
import { SectionBox } from "../components/home";
import CallToAction from "../components/cta";
import { postSendDownloadLink, regexPhone } from "../lib/utils";
import { Link } from "gatsby";

const featureCards = [
  {
    icon: "home/download.png",
    heading: "Download the app",
    description: "Sign up with any of the 10,000+ banks we support.",
  },
  {
    icon: "home/connect.png",
    heading: "Link your primary checking account",
    description: "Once connected, you account is ready to make payments.",
  },
  {
    icon: "home/txn.png",
    heading: "Make payments",
    description:
      "Use your phone as if itâ€™s your debit card to pay your favorite stores in a sigle swipe of a finger.",
  },
];

const statCards = [
  {
    heading: "We have supported",
    description: "35 local businesses",
  },
  {
    heading: "Raised",
    description: "$11,674",
  },
  {
    heading: "Remaining",
    description: "90 days",
  },
];

class IndexPage extends Component {
  constructor(props) {
    super(props);
    this.state = {
      contact: false,

      isLoading: false,
      isSuccess: false,
      isError: false,
      errorMsg: "",
      phone: "+1",
    };
  }

  componentDidMount() {
    mixpanel.track("HOME_PAGE_OPENED");
  }

  onChange = value => {
    this.setState({
      phone: value ? `+1${value.slice(2)}` : "+1",
      isError: false,
      isSuccess: false,
      errorMsg: "",
    });
  };

  onSubmit = async () => {
    this.setState({
      isSuccess: false,
      isError: false,
    });
    const { phone } = this.state;
    var error = false;
    if (!phone || regexPhone.test(phone) === false) {
      error = true;
      this.setState({
        isError: true,
        errorMsg: "Please enter a valid contact number",
      });
    }

    if (error === true) return;

    this.setState({ isLoading: true });
    const data = {
      phone_number: phone?.replace("+1", ""),
    };

    try {
      var res = await postSendDownloadLink(data);
      this.setState({ isLoading: false });
      if (res.ok) {
        this.setState({ isSuccess: true });
      } else if (res.status === 400) {
        this.setState({
          isError: true,
          errorMsg: "Please enter a valid contact number.",
        });
      } else {
        this.setState({
          isError: true,
          errorMsg: "Please enter a valid contact number.",
        });
      }
    } catch (error) {
      this.setState({
        isLoading: false,
        isError: true,
        errorMsg: "Please enter a valid contact number.",
      });
    }
  };

  render() {
    const headingClassName = "ma0 pa0 f3 f2-ns tc tl-l fw-600";
    const descriptionClassName = "f4 f3-ns ma0 mt4 mt10-ns tc tl-l fw-normal";
    const sideClassName = "col-12 col-6-l mt6 mt0-l";
    const sideImgClassName = "section-image";
    const bodyClassName = "col-12 col-6-l mw-s-l align-self-center";
    const sectionClassName = "pt20 pb10 pt30-ns pb30-ns";

    const { isLoading, isSuccess, isError, errorMsg, phone } = this.state;
    return (
      <Layout headerClass="">
        <SEO title="Priyanshu Jain's Blog" />
        <div className="main-content">
          <SectionBox
            heading={<span>Home for Athletes to Reach Their Full Potential</span>}
            headingClass="ma0 pa0 f2 f1-ns tc tl-l fw-bold"
            sideImg="home/home-hero.jpg"
            bodyClass="o1 o1-l col-12 col-5-l flex flex-column"
            sideImgClassName="hero-image"
            sideClass={`${sideClassName} o2 o2-l col-7-l justify-self-center justify-self-end-r`}
            className="hero-container"
          >
            <p className="f4 f3-ns ma0 mt4 mt10-ns tc tl-l fw-normal mb8">
              Priyanshu Jain's Blog
            </p>
            <div className="mt8">
              <div className="flex flex-column flex-row-ns align-center-m">
                <Link to="/athletes#laxmi">
                <button
                  className="btn-base btn-contained f5"
                  style={{
                    minWidth:240,
                    height: 60,
                  }}
                >
                  Support Our Athletes
                </button>
                </Link>
              </div>
            </div>
          </SectionBox>
          <CallToAction />
        </div>
      </Layout>
    );
  }
}
export default IndexPage;
